body {
  background-color: #eaeaea;
}

.ui.fixed.menu {
  background-image: linear-gradient(
    135deg,
    rgb(8, 10, 87) 0%,
    rgb(62, 130, 153) 83%,
    rgb(32, 167, 172) 95%
  );
}

.ui.main.container {
  margin-top: 7em;
}

.masthead {
  display: flex;
  align-items: center;
  background-image: linear-gradient(
    135deg,
    rgb(8, 10, 87) 0%,
    rgb(62, 130, 153) 83%,
    rgb(32, 167, 172) 95%
  ) !important;
  height: 100vh;
}

.masthead h1.ui.header {
  font-size: 4em;
}
